import { keyframes } from 'styled-components'

export const zoom = keyframes`
    0% {
        opacity: 0;
        transform: scale(0.9);
    } 
    to {
        opacity: 1;
        transform: scale(1);
    }

    // from {
    //     transform: rotate(0deg);
    // }
    // to {
    //     transform: rotate(360deg);
    // }

`
